/* Dashboard Styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", sans-serif;
    background-color: #f8fafc;
  }
  
  .dashboard {
    display: flex;
    height: 100vh;
    background-color: #f8fafc;
  }
  
  /* Sidebar Styles */
  .sidebar {
    position: fixed;
    top: 0;
    left: 0;
    width: 256px;
    height: 100vh;
    background: white;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
    transform: translateX(-100%);
    transition: transform 0.3s ease;
    z-index: 50;
  }
  
  .sidebar-open {
    transform: translateX(0);
  }
  
  @media (min-width: 1024px) {
    .sidebar {
      position: static;
      transform: translateX(0);
    }
  }
  
  .sidebar-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 64px;
    padding: 0 24px;
    border-bottom: 1px solid #e5e7eb;
  }
  
  .sidebar-brand {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  
  .brand-icon {
    font-size: 24px;
  }
  
  .brand-text {
    font-size: 18px;
    font-weight: 600;
    color: #111827;
  }
  
  .sidebar-close {
    display: block;
    background: none;
    border: none;
    font-size: 18px;
    color: #6b7280;
    cursor: pointer;
    padding: 4px;
    border-radius: 4px;
  }
  
  @media (min-width: 1024px) {
    .sidebar-close {
      display: none;
    }
  }
  
  .sidebar-nav {
    margin-top: 24px;
    padding: 0 12px;
  }
  
  .nav-item {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 12px;
    margin-bottom: 4px;
    background: none;
    border: none;
    border-radius: 6px;
    font-size: 14px;
    font-weight: 500;
    color: #6b7280;
    cursor: pointer;
    transition: all 0.2s;
    text-align: left;
  }
  
  .nav-item:hover {
    background-color: #f3f4f6;
    color: #111827;
  }
  
  .nav-item-active {
    background-color: #dbeafe;
    color: #1d4ed8;
  }
  
  /* Main Content */
  .main-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
  
  @media (min-width: 1024px) {
    .main-content {
      margin-left: 0;
    }
  }
  
  .header {
    display: flex;
    align-items: center;
    height: 64px;
    padding: 0 24px;
    background: white;
    border-bottom: 1px solid #e5e7eb;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  }
  
  .menu-button {
    display: block;
    background: none;
    border: none;
    font-size: 20px;
    color: #6b7280;
    cursor: pointer;
    padding: 8px;
    border-radius: 4px;
    margin-right: 16px;
  }
  
  @media (min-width: 1024px) {
    .menu-button {
      display: none;
    }
  }
  
  .header-title {
    font-size: 20px;
    font-weight: 600;
    color: #111827;
  }
  
  .content {
    flex: 1;
    overflow: auto;
    padding: 24px;
  }
  
  /* Stats Grid */
  .stats-grid {
    display: grid;
    grid-template-columns: 1fr;
    gap: 24px;
    margin-bottom: 32px;
  }
  
  @media (min-width: 768px) {
    .stats-grid {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (min-width: 1024px) {
    .stats-grid {
      grid-template-columns: repeat(4, 1fr);
    }
  }
  
  .stat-card {
    background: white;
    border-radius: 8px;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
    padding: 24px;
  }
  
  .stat-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .stat-label {
    font-size: 14px;
    font-weight: 500;
    color: #6b7280;
    margin-bottom: 4px;
  }
  
  .stat-value {
    font-size: 24px;
    font-weight: 700;
    color: #111827;
    margin-bottom: 4px;
  }
  
  .stat-description {
    font-size: 12px;
    color: #9ca3af;
  }
  
  .stat-icon {
    padding: 12px;
    border-radius: 50%;
    font-size: 24px;
  }
  
  .stat-icon-blue {
    background-color: #dbeafe;
  }
  
  .stat-icon-orange {
    background-color: #fed7aa;
  }
  
  .stat-icon-green {
    background-color: #dcfce7;
  }
  
  .stat-icon-purple {
    background-color: #e9d5ff;
  }
  
  /* Filter Card */
  .filter-card {
    background: white;
    border-radius: 8px;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
    padding: 24px;
    margin-bottom: 32px;
  }
  
  .filter-header {
    margin-bottom: 16px;
  }
  
  .filter-title {
    font-size: 18px;
    font-weight: 600;
    color: #111827;
    margin-bottom: 4px;
  }
  
  .filter-description {
    font-size: 14px;
    color: #6b7280;
  }
  
  .filter-content {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }
  
  @media (min-width: 640px) {
    .filter-content {
      flex-direction: row;
    }
  }
  
  .input-group {
    flex: 1;
  }
  
  .input-label {
    display: block;
    font-size: 14px;
    font-weight: 500;
    color: #374151;
    margin-bottom: 4px;
  }
  
  .input-field {
    width: 100%;
    padding: 8px 12px;
    border: 1px solid #d1d5db;
    border-radius: 6px;
    font-size: 14px;
    transition: border-color 0.2s;
  }
  
  .input-field:focus {
    outline: none;
    border-color: #3b82f6;
    box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
  }
  
  /* Table Container */
  .table-container {
    background: white;
    border-radius: 8px;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  }
  
  .tab-navigation {
    display: flex;
    border-bottom: 1px solid #e5e7eb;
    padding: 0 24px;
  }
  
  .tab-button {
    padding: 16px 4px;
    margin-right: 32px;
    background: none;
    border: none;
    border-bottom: 2px solid transparent;
    font-size: 14px;
    font-weight: 500;
    color: #6b7280;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .tab-button:hover {
    color: #374151;
    border-bottom-color: #d1d5db;
  }
  
  .tab-button-active {
    color: #2563eb;
    border-bottom-color: #2563eb;
  }
  
  .tab-content {
    padding: 24px;
  }
  
  .table-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 24px;
    flex-direction: column;
    gap: 16px;
  }
  
  @media (min-width: 640px) {
    .table-header {
      flex-direction: row;
      align-items: center;
      gap: 0;
    }
  }
  
  .table-title {
    font-size: 18px;
    font-weight: 600;
    color: #111827;
    margin-bottom: 4px;
  }
  
  .table-description {
    font-size: 14px;
    color: #6b7280;
  }
  
  .export-button {
    display: inline-flex;
    align-items: center;
    padding: 8px 16px;
    border: none;
    border-radius: 6px;
    font-size: 14px;
    font-weight: 500;
    color: white;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .export-button-blue {
    background-color: #2563eb;
  }
  
  .export-button-blue:hover {
    background-color: #1d4ed8;
  }
  
  .export-button-orange {
    background-color: #ea580c;
  }
  
  .export-button-orange:hover {
    background-color: #c2410c;
  }
  
  .export-button-green {
    background-color: #16a34a;
  }
  
  .export-button-green:hover {
    background-color: #15803d;
  }
  
  /* Table Styles */
  .table-wrapper {
    overflow-x: auto;
  }
  
  .data-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .data-table th {
    padding: 12px;
    text-align: left;
    font-size: 12px;
    font-weight: 500;
    color: #6b7280;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    background-color: #f9fafb;
    border-bottom: 1px solid #e5e7eb;
  }
  
  .data-table td {
    padding: 16px 12px;
    font-size: 14px;
    color: #111827;
    border-bottom: 1px solid #f3f4f6;
  }
  
  .data-table tbody tr:hover {
    background-color: #f9fafb;
  }
  
  .font-medium {
    font-weight: 500;
  }
  
  .font-mono {
    font-family: ui-monospace, SFMono-Regular, "SF Mono", Consolas, "Liberation Mono", Menlo, monospace;
  }
  
  /* Badge Styles */
  .badge {
    display: inline-flex;
    align-items: center;
    padding: 2px 10px;
    border-radius: 9999px;
    font-size: 12px;
    font-weight: 500;
  }
  
  .badge-blue {
    background-color: #dbeafe;
    color: #1e40af;
  }
  
  .badge-green {
    background-color: #dcfce7;
    color: #166534;
  }
  
  .badge-gray {
    background-color: #f3f4f6;
    color: #374151;
  }
  
  /* Overlay */
  .overlay {
    position: fixed;
    inset: 0;
    z-index: 40;
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  @media (min-width: 1024px) {
    .overlay {
      display: none;
    }
  }
  